const display = (route, name, color) => {
  console.log(color);
  map.addSource(name, {
    'type': 'geojson',
    'data': {
      'type': 'Feature',
      'properties': {},
      'geometry': {
        'type': 'LineString',
        'coordinates': route // [[lng, lat],[lng, lat]]
      }
    }
  });
  map.addLayer({
    'id': name,
    'type': 'line',
    'source': name,
    'layout': {
      'line-join': 'round',
      'line-cap': 'round'
    },
    'paint': {
      'line-color': color,
      'line-width': 2
    }
  });
};

const extractArray = () => {
  let coordinates = document.getElementById("hidden").dataset.price;
  let test = []
  coordinates = coordinates.split("],").forEach((yes) => {
    const qwe = yes.split("\"").join("").split("[").join("").split(",");
    let lng = Number.parseFloat(qwe[0].trim(), 10);
    let lat = Number.parseFloat(qwe[1].trim(), 10);
    let ele = Number.parseFloat(qwe[2].trim(), 10);
    let color = qwe[3].trim();
    test.push([lng, lat, ele, color]);
  });
  return test;
};

const displayRoute = () => {
  const test = extractArray();
  var map = new mapboxgl.Map({
    container: 'map',
    style: 'mapbox://styles/mapbox/outdoors-v11',
    center: [test[0][0], test[0][1]],
    zoom: 10
  });
  map.on("click", function(e) {
    for(let i = 1; i < test.length - 1; i++) {
      display([[test[i-1][0], test[i-1][1]],[test[i][0], test[i][1]]], i.toString(),test[i][3]);
    };
  });
};

export { displayRoute };